@import url(https://fonts.googleapis.com/css?family=Roboto:500);
.App {
  text-align: center;
  padding: 5% 5%;
}

.App * {
  box-sizing: border-box;
  padding-top: 1%;
}

h1,
h2 {
  color: #7c73e6;
}

/* Hide the dots */
ul {
  padding: 0;
  list-style: none;
}
/* 
